:root {
  --accent: #3b82f6;
  --accent-dark: #2563eb;
  --me: #c0392b;
  --text: #1f2937;
  --text-light: #6b7280;
  --border: #e5e7eb;
  --bg: #ffffff;
  --bg-gray: #f9fafb;
  --shadow: rgba(0, 0, 0, 0.1);
  --shadow-hover: rgba(0, 0, 0, 0.15);
  --sidebar-width: 280px;
  --container-width: 1024px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
  scroll-behavior: smooth;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  line-height: 1.6;
  color: var(--text);
  background: var(--bg);
  overflow-x: hidden;
}

.site-wrapper {
  display: flex;
  min-height: 100vh;
}

.container {
  max-width: var(--container-width);
  margin: 0 auto;
  padding: 0 1.5rem;
  width: 100%;
}

.sidebar {
  width: var(--sidebar-width);
  background: var(--bg);
  border-right: 1px solid var(--border);
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  z-index: 100;
  transition: transform 0.3s ease;
}

.sidebar-header {
  padding: 2rem 1.5rem 1rem;
  border-bottom: 1px solid var(--border);
}

.sidebar-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--text);
  text-decoration: none;
  display: block;
  margin-bottom: 0.25rem;
}

.sidebar-title:hover {
  color: var(--accent);
}

.sidebar-subtitle {
  font-size: 0.875rem;
  color: var(--text-light);
  line-height: 1.4;
}

.sidebar-nav {
  flex: 1;
  padding: 1.5rem 0;
}

.sidebar-nav ul {
  list-style: none;
}

.sidebar-nav li {
  margin: 0;
}

.sidebar-nav a {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 1.5rem;
  color: var(--text);
  text-decoration: none;
  transition: all 0.2s ease;
  font-weight: 500;
}

.sidebar-nav a:hover,
.sidebar-nav a.active {
  background: var(--bg-gray);
  color: var(--accent);
}

.sidebar-nav svg {
  flex-shrink: 0;
}

.sidebar-footer {
  padding: 1.5rem;
  border-top: 1px solid var(--border);
}

.social-links {
  display: flex;
  gap: 0.75rem;
  flex-wrap: wrap;
}

.social-links a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  border-radius: 8px;
  background: var(--bg-gray);
  color: var(--text);
  transition: all 0.2s ease;
}

.social-links a:hover {
  background: var(--accent);
  color: white;
  transform: translateY(-2px);
}

.main-content {
  flex: 1;
  margin-left: var(--sidebar-width);
  min-height: 100vh;
}

.mobile-menu-toggle {
  display: none;
  position: fixed;
  top: 1rem;
  left: 1rem;
  z-index: 101;
  background: var(--accent);
  border: none;
  border-radius: 8px;
  width: 44px;
  height: 44px;
  cursor: pointer;
  box-shadow: 0 2px 8px var(--shadow);
}

.hamburger {
  display: block;
  width: 20px;
  height: 2px;
  background: white;
  position: relative;
  margin: 0 auto;
}

.hamburger::before,
.hamburger::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 2px;
  background: white;
  left: 0;
  transition: all 0.3s ease;
}

.hamburger::before {
  top: -6px;
}

.hamburger::after {
  top: 6px;
}

.mobile-menu-toggle[aria-expanded="true"] .hamburger {
  background: transparent;
}

.mobile-menu-toggle[aria-expanded="true"] .hamburger::before {
  transform: rotate(45deg);
  top: 0;
}

.mobile-menu-toggle[aria-expanded="true"] .hamburger::after {
  transform: rotate(-45deg);
  top: 0;
}

.hero {
  background: linear-gradient(135deg, var(--bg-gray) 0%, var(--bg) 100%);
  padding: 4rem 0;
  text-align: center;
}

.hero-content {
  max-width: 700px;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.hero-portrait {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid white;
  box-shadow: 0 4px 12px var(--shadow);
  margin-bottom: 1.5rem;
}

.hero-name {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
  line-height: 1.2;
}

.hero-title {
  font-size: 1.125rem;
  color: var(--text-light);
  margin-bottom: 0.25rem;
}

.hero-location {
  font-size: 1rem;
  color: var(--text-light);
  margin-bottom: 1.5rem;
}

.hero-banner {
  background: var(--accent);
  color: white;
  padding: 1rem 2rem;
  border-radius: 12px;
  margin: 2rem auto;
  display: inline-block;
  font-size: 1.25rem;
  font-weight: 800;
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

.hero-actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 2rem;
}

.section {
  padding: 4rem 0;
}

.section-gray {
  background: var(--bg-gray);
}

.section h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  line-height: 1.2;
}

.intro {
  font-size: 1.125rem;
  line-height: 1.8;
  color: var(--text);
  margin-bottom: 2rem;
}

.timeline {
  list-style: none;
  padding-left: 1.5rem;
}

.timeline li {
  position: relative;
  padding-left: 1.5rem;
  margin-bottom: 1rem;
  line-height: 1.6;
}

.timeline li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0.5rem;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--accent);
}

.interests {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

.badge {
  display: inline-block;
  padding: 0.5rem 1rem;
  background: white;
  border: 1px solid var(--border);
  border-radius: 8px;
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--text);
  transition: all 0.2s ease;
}

.badge:hover {
  border-color: var(--accent);
  color: var(--accent);
}

.badge-journal {
  background: #dbeafe;
  color: #1e40af;
  border-color: #93c5fd;
}

.badge-conference {
  background: #fce7f3;
  color: #9f1239;
  border-color: #fbcfe8;
}

.btn {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.2s ease;
  border: none;
  cursor: pointer;
  font-size: 1rem;
  text-align: center;
}

.btn-primary {
  background: var(--accent);
  color: white;
}

.btn-primary:hover {
  background: var(--accent-dark);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px var(--shadow-hover);
}

.btn-secondary {
  background: white;
  color: var(--accent);
  border: 2px solid var(--accent);
}

.btn-secondary:hover {
  background: var(--accent);
  color: white;
  transform: translateY(-2px);
}

.pub-card {
  background: white;
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  transition: all 0.2s ease;
}

.pub-card:hover {
  box-shadow: 0 4px 12px var(--shadow);
  transform: translateY(-2px);
}

.pub-title {
  font-size: 1.25rem;
  font-weight: 700;
  margin-bottom: 0.75rem;
  line-height: 1.3;
}

.pub-title a {
  color: var(--text);
  text-decoration: none;
}

.pub-title a:hover {
  color: var(--accent);
}

.pub-authors {
  font-size: 0.95rem;
  color: var(--text-light);
  margin-bottom: 0.5rem;
  line-height: 1.5;
}

.me {
  font-weight: 700;
  color: var(--me);
}

.pub-venue {
  font-size: 0.95rem;
  margin-bottom: 0.75rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.pub-abstract {
  font-size: 0.95rem;
  line-height: 1.6;
  color: var(--text-light);
  margin-bottom: 1rem;
}

.pub-links {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.pub-link {
  display: inline-block;
  padding: 0.375rem 0.75rem;
  background: var(--bg-gray);
  color: var(--text);
  text-decoration: none;
  border-radius: 6px;
  font-size: 0.875rem;
  font-weight: 500;
  transition: all 0.2s ease;
}

.pub-link:hover {
  background: var(--accent);
  color: white;
}

.publications-list {
  margin-bottom: 2rem;
}

.publication-detail {
  background: white;
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 2rem;
  margin-bottom: 2rem;
}

.pub-meta {
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid var(--border);
}

.pub-navigation {
  margin-top: 3rem;
  padding-top: 2rem;
  border-top: 1px solid var(--border);
}

.page-header {
  background: var(--bg-gray);
  padding: 3rem 0 2rem;
  margin-bottom: 3rem;
}

.page-header h1 {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
}

.page-description {
  font-size: 1.125rem;
  color: var(--text-light);
}

.post-meta {
  font-size: 0.95rem;
  color: var(--text-light);
}

.post-content {
  max-width: 720px;
  margin: 0 auto 3rem;
  line-height: 1.8;
}

.post-content h2 {
  font-size: 1.75rem;
  margin-top: 2rem;
  margin-bottom: 1rem;
}

.post-content h3 {
  font-size: 1.5rem;
  margin-top: 1.5rem;
  margin-bottom: 0.75rem;
}

.post-content p {
  margin-bottom: 1.25rem;
}

.post-content img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  margin: 2rem 0;
}

.post-content ul,
.post-content ol {
  margin-bottom: 1.25rem;
  padding-left: 2rem;
}

.post-content li {
  margin-bottom: 0.5rem;
}

.post-content code {
  background: var(--bg-gray);
  padding: 0.2rem 0.4rem;
  border-radius: 4px;
  font-family: "Courier New", monospace;
  font-size: 0.9em;
}

.post-content pre {
  background: var(--bg-gray);
  padding: 1rem;
  border-radius: 8px;
  overflow-x: auto;
  margin-bottom: 1.25rem;
}

.post-content pre code {
  background: none;
  padding: 0;
}

.post-navigation {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  margin-top: 3rem;
  padding-top: 2rem;
  border-top: 1px solid var(--border);
}

.post-nav-prev,
.post-nav-next {
  color: var(--accent);
  text-decoration: none;
  font-weight: 600;
  transition: color 0.2s ease;
}

.post-nav-prev:hover,
.post-nav-next:hover {
  color: var(--accent-dark);
}

.blog-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.blog-card {
  background: white;
  border: 1px solid var(--border);
  border-radius: 12px;
  overflow: hidden;
  transition: all 0.2s ease;
  text-decoration: none;
  color: var(--text);
  display: block;
}

.blog-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 20px var(--shadow);
}

.blog-card-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  background: var(--bg-gray);
}

.blog-card-content {
  padding: 1.5rem;
}

.blog-card-title {
  font-size: 1.25rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  line-height: 1.3;
}

.blog-card-date {
  font-size: 0.875rem;
  color: var(--text-light);
  margin-bottom: 0.75rem;
}

.blog-card-excerpt {
  font-size: 0.95rem;
  line-height: 1.6;
  color: var(--text-light);
}

.pagination {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 3rem;
}

.pagination a,
.pagination span {
  display: inline-block;
  padding: 0.5rem 1rem;
  border: 1px solid var(--border);
  border-radius: 6px;
  text-decoration: none;
  color: var(--text);
  transition: all 0.2s ease;
}

.pagination a:hover {
  border-color: var(--accent);
  color: var(--accent);
}

.pagination .current {
  background: var(--accent);
  color: white;
  border-color: var(--accent);
}

.cv-embed {
  width: 100%;
  height: 80vh;
  border: 1px solid var(--border);
  border-radius: 12px;
  margin-bottom: 2rem;
}

.contact-list {
  list-style: none;
  margin-bottom: 3rem;
}

.contact-list li {
  margin-bottom: 1rem;
}

.contact-list a {
  color: var(--accent);
  text-decoration: none;
  font-weight: 500;
  font-size: 1.125rem;
  transition: color 0.2s ease;
}

.contact-list a:hover {
  color: var(--accent-dark);
  text-decoration: underline;
}

.office-hours {
  background: var(--bg-gray);
  padding: 2rem;
  border-radius: 12px;
  margin-bottom: 2rem;
}

.office-hours h2 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.office-hours p {
  font-size: 1.125rem;
  line-height: 1.6;
}

.text-center {
  text-align: center;
}

a:focus-visible,
button:focus-visible {
  outline: 2px solid var(--accent);
  outline-offset: 2px;
}

@media (max-width: 768px) {
  .sidebar {
    transform: translateX(-100%);
  }

  .sidebar.is-open {
    transform: translateX(0);
  }

  .mobile-menu-toggle {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .main-content {
    margin-left: 0;
    padding-top: 4rem;
  }

  .hero {
    padding: 3rem 0;
  }

  .hero-name {
    font-size: 2rem;
  }

  .hero-banner {
    font-size: 1.125rem;
    padding: 0.875rem 1.5rem;
  }

  .section {
    padding: 3rem 0;
  }

  .section h2 {
    font-size: 1.75rem;
  }

  .page-header h1 {
    font-size: 2rem;
  }

  .blog-list {
    grid-template-columns: 1fr;
  }

  .hero-actions {
    flex-direction: column;
  }

  .post-navigation {
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  .hero-name {
    font-size: 1.75rem;
  }

  .hero-portrait {
    width: 150px;
    height: 150px;
  }
}

@media print {
  .sidebar,
  .mobile-menu-toggle,
  .hero-actions,
  .pub-navigation,
  .post-navigation {
    display: none;
  }

  .main-content {
    margin-left: 0;
  }
}
